@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.leaflet-container {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
    height: 400px;
    width: 90%;
    max-width: 800px;
    z-index: 0;
    width: 100%;
}
.leaflet-control-container .leaflet-routing-container-hide {
    display: none;
}

@media only screen and (max-width: 600px) {
  .leaflet-container {
    
    margin-left: 0;
    box-shadow: none;
  }
}
/* reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    color: rgb(0, 126, 0);
    text-decoration: unset;
}

ul {
    margin-left: 1.5rem;
    list-style: none;
}

.defaultCursor {
    cursor: default;
}

.pointer {
    cursor: pointer;
}
button {
    border: none;
    background: none;
    color: unset;
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
    margin: 1em 0;
}

/* general */

body {
    font-family: 'Roboto', sans-serif;
    background-color:#f9f8eb; 
}
